<template><undefined>
<h1>Select 选择器</h1>
<p>类似 Select2 的选择器。</p>
<h2 id="he-shi-shi-yong"><a class="header-anchor" href="#he-shi-shi-yong" aria-hidden="true">¶</a> 何时使用</h2>
<ul>
<li>弹出一个下拉菜单给用户选择操作，用于代替原生的选择器，或者需要一个更优雅的多选器时。</li>
<li>当选项少时（少于 5 项），建议直接将选项平铺，使用 Radio 是更好的选择。</li>
</ul>
<h2 id="dai-ma-yan-shi"><a class="header-anchor" href="#dai-ma-yan-shi" aria-hidden="true">¶</a> 代码演示</h2>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n    &lt;v-select placeholder=\&quot;请选择人员\&quot; style=\&quot;width: 120px;\&quot; :data=\&quot;options\&quot; @change=\&quot;change\&quot;&gt;&lt;/v-select&gt;\n    &lt;v-select placement=\&quot;top\&quot; style=\&quot;width: 120px;\&quot; dropdown-width=\&quot;240px\&quot; :data=\&quot;options\&quot; v-model=\&quot;value\&quot;&gt;&lt;/v-select&gt;\n    &lt;v-select disabled style=\&quot;width: 120px;\&quot;&gt;&lt;/v-select&gt;\n&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\n    export default {\n        data: ()=&gt; ({\n            options: [{\n                value: '1',\n                label: 'lady'\n            }, {\n                value: '2',\n                label: '小强',\n                disabled: true\n            }, {\n                value: '3',\n                label: '小明'\n            }],\n            value: '3'\n        }),\n        watch: {\n            value(val){\n                console.log(val)\n            }\n        },\n        methods: {\n            change(val) {\n                console.log(val)\n            }\n        }\n    }\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
    <v-select placeholder="请选择人员" style="width: 120px;" :data="options" @change="change"></v-select>
    <v-select placement="top" style="width: 120px;" dropdown-width="240px" :data="options" v-model="value"></v-select>
    <v-select disabled style="width: 120px;"></v-select>
</template>


</div>
                  <div slot="description"><h4 id="ji-ben"><a class="header-anchor" href="#ji-ben" aria-hidden="true">¶</a> 基本</h4>
<p>基本使用。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-select</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请选择人员"</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"width: 120px;"</span> <span class="hljs-attr">:data</span>=<span class="hljs-string">"options"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"change"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-select</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-select</span> <span class="hljs-attr">placement</span>=<span class="hljs-string">"top"</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"width: 120px;"</span> <span class="hljs-attr">dropdown-width</span>=<span class="hljs-string">"240px"</span> <span class="hljs-attr">:data</span>=<span class="hljs-string">"options"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-select</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-select</span> <span class="hljs-attr">disabled</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"width: 120px;"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-select</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        <span class="hljs-attr">data</span>: <span class="hljs-function"><span class="hljs-params">()</span>=&gt;</span> ({
            <span class="hljs-attr">options</span>: [{
                <span class="hljs-attr">value</span>: <span class="hljs-string">'1'</span>,
                <span class="hljs-attr">label</span>: <span class="hljs-string">'lady'</span>
            }, {
                <span class="hljs-attr">value</span>: <span class="hljs-string">'2'</span>,
                <span class="hljs-attr">label</span>: <span class="hljs-string">'小强'</span>,
                <span class="hljs-attr">disabled</span>: <span class="hljs-literal">true</span>
            }, {
                <span class="hljs-attr">value</span>: <span class="hljs-string">'3'</span>,
                <span class="hljs-attr">label</span>: <span class="hljs-string">'小明'</span>
            }],
            <span class="hljs-attr">value</span>: <span class="hljs-string">'3'</span>
        }),
        <span class="hljs-attr">watch</span>: {
            value(val){
                <span class="hljs-built_in">console</span>.log(val)
            }
        },
        <span class="hljs-attr">methods</span>: {
            change(val) {
                <span class="hljs-built_in">console</span>.log(val)
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div>
                <summary>
