<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n    &lt;v-select style=\&quot;width: 100%\&quot; :data=\&quot;options\&quot; v-model=\&quot;value\&quot;&gt;\n        &lt;template slot-scope=\&quot;{data}\&quot;&gt;\n            {{data.label}}-{{data.value}}\n        &lt;/template&gt;\n    &lt;/v-select&gt;\n&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\n    export default {\n        data: ()=&gt; ({\n            options: [{\n                value: '1',\n                label: 'lady'\n            }, {\n                value: '2',\n                label: '小强',\n                disabled: true\n            }, {\n                value: '3',\n                label: '小明'\n            }],\n            value: '3'\n        }),\n        watch: {\n            value(val){\n                console.log(val)\n            }\n        }\n    }\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
    <v-select style="width: 100%" :data="options" v-model="value">
        <template slot-scope="{data}">
            {{data.label}}-{{data.value}}
        </template>
    </v-select>
</template>


</div>
                  <div slot="description"><h4 id="zi-ding-yi-xuan-xiang"><a class="header-anchor" href="#zi-ding-yi-xuan-xiang" aria-hidden="true">¶</a> 自定义选项</h4>
