                  <div slot="description"><h4 id="sou-suo-guo-lu"><a class="header-anchor" href="#sou-suo-guo-lu" aria-hidden="true">¶</a> 搜索过滤</h4>
<p>使用 <code>filter</code> 进行自定义的搜索</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-select</span> <span class="hljs-attr">search</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"width: 120px;"</span> <span class="hljs-attr">:filter</span>=<span class="hljs-string">"filter"</span> <span class="hljs-attr">:data</span>=<span class="hljs-string">"filterOptions"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-select</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">br</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">br</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-select</span> <span class="hljs-attr">search</span> <span class="hljs-attr">multiple</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"width: 100%"</span> <span class="hljs-attr">:filter</span>=<span class="hljs-string">"filter"</span> <span class="hljs-attr">:data</span>=<span class="hljs-string">"filterOptions"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-select</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
     <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
         <span class="hljs-attr">data</span>: <span class="hljs-function"><span class="hljs-params">()</span>=&gt;</span> ({
             <span class="hljs-attr">filterOptions</span>: [{
                <span class="hljs-attr">value</span>: <span class="hljs-string">'1'</span>,
                <span class="hljs-attr">label</span>: <span class="hljs-string">'河北'</span>,
                <span class="hljs-attr">jp</span>: <span class="hljs-string">'HB'</span>,
                <span class="hljs-attr">qp</span>: <span class="hljs-string">'HeiBei'</span>,
            }, {
                <span class="hljs-attr">value</span>: <span class="hljs-string">'2'</span>,
                <span class="hljs-attr">label</span>: <span class="hljs-string">'河南'</span>,
                <span class="hljs-attr">jp</span>: <span class="hljs-string">'HN'</span>,
                <span class="hljs-attr">qp</span>: <span class="hljs-string">'HeiNan'</span>,
            }, {
                <span class="hljs-attr">value</span>: <span class="hljs-string">'3'</span>,
                <span class="hljs-attr">label</span>: <span class="hljs-string">'海南'</span>,
                <span class="hljs-attr">jp</span>: <span class="hljs-string">'HN'</span>,
                <span class="hljs-attr">qp</span>: <span class="hljs-string">'HaiNan'</span>,
            }],
         }),
         <span class="hljs-attr">methods</span>: {
             filter(val, item) {
                <span class="hljs-keyword">const</span> input = val.toLocaleUpperCase();
                <span class="hljs-keyword">return</span> item.label.startsWith(input) ||
                        item.jp.startsWith(input) ||
                        item.qp.startsWith(input);
            },
         }
     }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div>
