                    <span class="hljs-attr">value</span>: <span class="hljs-string">'wz'</span>,
                    <span class="hljs-attr">label</span>: <span class="hljs-string">'万州'</span>,
                    <span class="hljs-attr">disabled</span>: <span class="hljs-literal">true</span>
                }]
            }, {
                <span class="hljs-attr">label</span>: <span class="hljs-string">'四川'</span>,
                <span class="hljs-attr">options</span>: [{
                    <span class="hljs-attr">value</span>: <span class="hljs-string">'cd'</span>,
                    <span class="hljs-attr">label</span>: <span class="hljs-string">'成都'</span>
                }, {
                    <span class="hljs-attr">value</span>: <span class="hljs-string">'dz'</span>,
                    <span class="hljs-attr">label</span>: <span class="hljs-string">'达州'</span>
                }]
            }]
        })
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</demo-box>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n    &lt;v-select style=\&quot;width: 200px\&quot; search :loading=\&quot;loading\&quot; :remote-method=\&quot;remoteMethod\&quot; :data=\&quot;remoteOption\&quot;&gt;&lt;/v-select&gt;\n    &lt;br&gt;&lt;br&gt;\n    &lt;v-select style=\&quot;width: 100%\&quot; search multiple :loading=\&quot;loading2\&quot; :remote-method=\&quot;remoteMethod2\&quot; :data=\&quot;remoteOption\&quot;&gt;&lt;/v-select&gt;\n&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\n    export default {\n        data: ()=&gt; ({\n            list: [],\n            states: [\&quot;Alabama\&quot;, \&quot;Alaska\&quot;, \&quot;Arizona\&quot;,\n                \&quot;Arkansas\&quot;, \&quot;California\&quot;, \&quot;Colorado\&quot;,\n                \&quot;Connecticut\&quot;, \&quot;Delaware\&quot;, \&quot;Florida\&quot;,\n                \&quot;Georgia\&quot;, \&quot;Hawaii\&quot;, \&quot;Idaho\&quot;, \&quot;Illinois\&quot;,\n                \&quot;Indiana\&quot;, \&quot;Iowa\&quot;, \&quot;Kansas\&quot;, \&quot;Kentucky\&quot;,\n                \&quot;Louisiana\&quot;, \&quot;Maine\&quot;, \&quot;Maryland\&quot;,\n                \&quot;Massachusetts\&quot;, \&quot;Michigan\&quot;, \&quot;Minnesota\&quot;,\n                \&quot;Mississippi\&quot;, \&quot;Missouri\&quot;, \&quot;Montana\&quot;,\n                \&quot;Nebraska\&quot;, \&quot;Nevada\&quot;, \&quot;New Hampshire\&quot;,\n                \&quot;New Jersey\&quot;, \&quot;New Mexico\&quot;, \&quot;New York\&quot;,\n                \&quot;North Carolina\&quot;, \&quot;North Dakota\&quot;, \&quot;Ohio\&quot;,\n                \&quot;Oklahoma\&quot;, \&quot;Oregon\&quot;, \&quot;Pennsylvania\&quot;,\n                \&quot;Rhode Island\&quot;, \&quot;South Carolina\&quot;,\n                \&quot;South Dakota\&quot;, \&quot;Tennessee\&quot;, \&quot;Texas\&quot;,\n                \&quot;Utah\&quot;, \&quot;Vermont\&quot;, \&quot;Virginia\&quot;,\n                \&quot;Washington\&quot;, \&quot;West Virginia\&quot;, \&quot;Wisconsin\&quot;,\n                \&quot;Wyoming\&quot;],\n            loading: false,\n            loading2: false,\n            remoteOption: []\n        }),\n        mounted(){\n            this.list = this.states.map(item =&gt; {\n                return { value: item, label: item };\n            });\n        },\n        methods: {\n            remoteMethod(query) {\n                if (query !== '') {\n                    this.loading = true;\n                    setTimeout(() =&gt; {\n                        this.loading = false;\n                        this.remoteOption = this.list.filter(item =&gt; {\n                            return item.label.toLowerCase().indexOf(query.toLowerCase()) &gt; -1;\n                        });\n                    }, 200);\n                } else {\n                    this.remoteOption = [];\n                }\n            },\n            remoteMethod2(query) {\n                if (query !== '') {\n                    this.loading2 = true;\n                    setTimeout(() =&gt; {\n                        this.loading2 = false;\n                        this.remoteOption = this.list.filter(item =&gt; {\n                            return item.label.toLowerCase().indexOf(query.toLowerCase()) &gt; -1;\n                        });\n                    }, 200);\n                } else {\n                    this.remoteOption = [];\n                }\n            }\n        }\n    }\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
    <v-select style="width: 200px" search="" :loading="loading" :remote-method="remoteMethod" :data="remoteOption"></v-select>
    <br><br>
    <v-select style="width: 100%" search="" multiple :loading="loading2" :remote-method="remoteMethod2" :data="remoteOption"></v-select>
</template>


</div>
                  <div slot="description"><h4 id="yuan-cheng-sou-suo"><a class="header-anchor" href="#yuan-cheng-sou-suo" aria-hidden="true">¶</a> 远程搜索</h4>
<p>从服务器搜索数据，输入关键字进行查找</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-select</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"width: 200px"</span> <span class="hljs-attr">search</span> <span class="hljs-attr">:loading</span>=<span class="hljs-string">"loading"</span> <span class="hljs-attr">:remote-method</span>=<span class="hljs-string">"remoteMethod"</span> <span class="hljs-attr">:data</span>=<span class="hljs-string">"remoteOption"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-select</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">br</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">br</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-select</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"width: 100%"</span> <span class="hljs-attr">search</span> <span class="hljs-attr">multiple</span> <span class="hljs-attr">:loading</span>=<span class="hljs-string">"loading2"</span> <span class="hljs-attr">:remote-method</span>=<span class="hljs-string">"remoteMethod2"</span> <span class="hljs-attr">:data</span>=<span class="hljs-string">"remoteOption"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-select</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        <span class="hljs-attr">data</span>: <span class="hljs-function"><span class="hljs-params">()</span>=&gt;</span> ({
            <span class="hljs-attr">list</span>: [],
            <span class="hljs-attr">states</span>: [<span class="hljs-string">"Alabama"</span>, <span class="hljs-string">"Alaska"</span>, <span class="hljs-string">"Arizona"</span>,
                <span class="hljs-string">"Arkansas"</span>, <span class="hljs-string">"California"</span>, <span class="hljs-string">"Colorado"</span>,
                <span class="hljs-string">"Connecticut"</span>, <span class="hljs-string">"Delaware"</span>, <span class="hljs-string">"Florida"</span>,
                <span class="hljs-string">"Georgia"</span>, <span class="hljs-string">"Hawaii"</span>, <span class="hljs-string">"Idaho"</span>, <span class="hljs-string">"Illinois"</span>,
                <span class="hljs-string">"Indiana"</span>, <span class="hljs-string">"Iowa"</span>, <span class="hljs-string">"Kansas"</span>, <span class="hljs-string">"Kentucky"</span>,
                <span class="hljs-string">"Louisiana"</span>, <span class="hljs-string">"Maine"</span>, <span class="hljs-string">"Maryland"</span>,
                <span class="hljs-string">"Massachusetts"</span>, <span class="hljs-string">"Michigan"</span>, <span class="hljs-string">"Minnesota"</span>,
                <span class="hljs-string">"Mississippi"</span>, <span class="hljs-string">"Missouri"</span>, <span class="hljs-string">"Montana"</span>,
                <span class="hljs-string">"Nebraska"</span>, <span class="hljs-string">"Nevada"</span>, <span class="hljs-string">"New Hampshire"</span>,
                <span class="hljs-string">"New Jersey"</span>, <span class="hljs-string">"New Mexico"</span>, <span class="hljs-string">"New York"</span>,
                <span class="hljs-string">"North Carolina"</span>, <span class="hljs-string">"North Dakota"</span>, <span class="hljs-string">"Ohio"</span>,
                <span class="hljs-string">"Oklahoma"</span>, <span class="hljs-string">"Oregon"</span>, <span class="hljs-string">"Pennsylvania"</span>,
                <span class="hljs-string">"Rhode Island"</span>, <span class="hljs-string">"South Carolina"</span>,
                <span class="hljs-string">"South Dakota"</span>, <span class="hljs-string">"Tennessee"</span>, <span class="hljs-string">"Texas"</span>,
                <span class="hljs-string">"Utah"</span>, <span class="hljs-string">"Vermont"</span>, <span class="hljs-string">"Virginia"</span>,
                <span class="hljs-string">"Washington"</span>, <span class="hljs-string">"West Virginia"</span>, <span class="hljs-string">"Wisconsin"</span>,
                <span class="hljs-string">"Wyoming"</span>],
            <span class="hljs-attr">loading</span>: <span class="hljs-literal">false</span>,
            <span class="hljs-attr">loading2</span>: <span class="hljs-literal">false</span>,
            <span class="hljs-attr">remoteOption</span>: []
        }),
        mounted(){
            <span class="hljs-keyword">this</span>.list = <span class="hljs-keyword">this</span>.states.map(<span class="hljs-function"><span class="hljs-params">item</span> =&gt;</span> {
                <span class="hljs-keyword">return</span> { <span class="hljs-attr">value</span>: item, <span class="hljs-attr">label</span>: item };
            });
        },
        <span class="hljs-attr">methods</span>: {
            remoteMethod(query) {
                <span class="hljs-keyword">if</span> (query !== <span class="hljs-string">''</span>) {
                    <span class="hljs-keyword">this</span>.loading = <span class="hljs-literal">true</span>;
                    setTimeout(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
                        <span class="hljs-keyword">this</span>.loading = <span class="hljs-literal">false</span>;
                        <span class="hljs-keyword">this</span>.remoteOption = <span class="hljs-keyword">this</span>.list.filter(<span class="hljs-function"><span class="hljs-params">item</span> =&gt;</span> {
                            <span class="hljs-keyword">return</span> item.label.toLowerCase().indexOf(query.toLowerCase()) &gt; <span class="hljs-number">-1</span>;
                        });
                    }, <span class="hljs-number">200</span>);
                } <span class="hljs-keyword">else</span> {
                    <span class="hljs-keyword">this</span>.remoteOption = [];
                }
            },
            remoteMethod2(query) {
                <span class="hljs-keyword">if</span> (query !== <span class="hljs-string">''</span>) {
                    <span class="hljs-keyword">this</span>.loading2 = <span class="hljs-literal">true</span>;
                    setTimeout(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
                        <span class="hljs-keyword">this</span>.loading2 = <span class="hljs-literal">false</span>;
                        <span class="hljs-keyword">this</span>.remoteOption = <span class="hljs-keyword">this</span>.list.filter(<span class="hljs-function"><span class="hljs-params">item</span> =&gt;</span> {
                            <span class="hljs-keyword">return</span> item.label.toLowerCase().indexOf(query.toLowerCase()) &gt; <span class="hljs-number">-1</span>;
                        });
                    }, <span class="hljs-number">200</span>);
                } <span class="hljs-keyword">else</span> {
                    <span class="hljs-keyword">this</span>.remoteOption = [];
                }
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div>
