<h2 id="api"><a class="header-anchor" href="#api" aria-hidden="true">¶</a> API</h2>
<h3 id="select-props"><a class="header-anchor" href="#select-props" aria-hidden="true">¶</a> Select Props</h3>
<table class="table"><thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value</td>
<td>指定默认选中的条目</td>
<td>string/array</td>
<td>-</td>
</tr>
<tr>
<td>default:scopedSlot</td>
<td>自定义下拉框选项内容,传入的参数：data</td>
<td>slot node</td>
<td>-</td>
</tr>
<tr>
<td>clue</td>
<td>选项的value的字段名</td>
<td>string</td>
<td>value</td>
</tr>
<tr>
<td>label</td>
<td>选项显示的文本的字段名</td>
<td>string</td>
<td>label</td>
</tr>
<tr>
<td>groupLabel</td>
<td>分组title的字段名</td>
<td>string</td>
<td>label</td>
</tr>
<tr>
<td>data</td>
<td>下拉框的数据</td>
<td>array</td>
<td>[]</td>
</tr>
<tr>
<td>multiple</td>
<td>是否支持多选</td>
<td>boolean</td>
<td>false</td>
</tr>
<tr>
<td>notFoundContent</td>
<td>当下拉列表为空时显示的内容</td>
<td>string</td>
<td>没有找到</td>
</tr>
<tr>
<td>placement</td>
<td>下拉框出现的位置(top、bottom)</td>
<td>string</td>
<td>bottom</td>
</tr>
<tr>
<td>search</td>
<td>是否可以搜索</td>
<td>boolean</td>
<td>false</td>
</tr>
<tr>
<td>filter</td>
<td>搜索过滤函数,返回Boolean</td>
<td>Function(value, item)</td>
<td>-</td>
</tr>
<tr>
<td>maxHeight</td>
<td>下拉框的最大高度</td>
<td>number</td>
<td>300</td>
</tr>
<tr>
<td>dropdownWidth</td>
<td>下拉框宽度</td>
<td>string</td>
<td>-</td>
</tr>
<tr>
<td>disabled</td>
<td>控件是否禁用</td>
<td>boolean</td>
<td>false</td>
</tr>
<tr>
<td>allowClear</td>
<td>支持清除, 单选模式有效</td>
<td>boolean</td>
<td>true</td>
</tr>
<tr>
<td>placeholder</td>
<td>选择框默认文字</td>
<td>string</td>
<td>请选择</td>
</tr>
<tr>
<td>size</td>
<td>选择框大小，可选 lg sm</td>
<td>string</td>
<td>-</td>
</tr>
<tr>
<td>loading</td>
<td>呈现加载样式（一般用于从远程获取数据）</td>
<td>boolean</td>
<td>false</td>
</tr>
<tr>
<td>loadingText</td>
<td>加载时显示的文字</td>
<td>string</td>
<td>加载中...</td>
</tr>
<tr>
<td>optionOnChange</td>
<td>指定change事件返回的数据是否是选中的整个option数据</td>
<td>boolean</td>
<td>false</td>
</tr>
<tr>
<td>remoteMethod</td>
<td>远程搜索方法</td>
<td>function</td>
<td>-</td>
</tr>
<tr>
<td>position</td>
<td>下拉框的定位方式（absolute,fixed）</td>
<td>string</td>
<td>absolute</td>
</tr>
<tr>
<td>popupContainer</td>
<td>下拉菜单渲染父节点。默认渲染到 body 上，如果你遇到菜单滚动定位问题，试试修改为滚动的区域，并相对其定位。</td>
<td>function</td>
<td>() =&gt; document.body</td>
</tr>
</tbody>
</table>
<h3 id="data-props"><a class="header-anchor" href="#data-props" aria-hidden="true">¶</a> Data Props</h3>
<table class="table"><thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value</td>
<td>选项的值(该字段可通过select的clue属性修改)</td>
<td>string/number/object</td>
<td>-</td>
</tr>
<tr>
<td>label</td>
<td>选项的标签或组名(作为标签时可通过select的label属性修改；作为组名时只支持一级,可通过select的groupLabel属性修改)</td>
<td>string</td>
<td>-</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用</td>
<td>boolean</td>
<td>false</td>
</tr>
<tr>
<td>options</td>
<td>分组的数据</td>
<td>array</td>
<td>-</td>
</tr>
</tbody>
</table>
<h3 id="select-events"><a class="header-anchor" href="#select-events" aria-hidden="true">¶</a> Select Events</h3>
<table class="table"><thead>
<tr>
<th>事件</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>change</td>
<td>选择的值发生变化的时候触发，默认返回value，如需返回整个option，请设置optionOnChange</td>
<td>value</td>
</tr>
<tr>
<td>focus</td>
<td>focus事件</td>
<td>-</td>
</tr>
<tr>
<td>blur</td>
<td>blur事件</td>
<td>-</td>
</tr>
<tr>
<td>search</td>
<td>文本框值变化时回调</td>
<td>value</td>
</tr>
</tbody>
</table>
</undefined></template>

<script>
    export default {
        data: () => ({
            options: [{
                value: '1',
                label: 'lady'
            }, {
                value: '2',
                label: '小强',
                disabled: true
            }, {
                value: '3',
                label: '小明'
            }],
            filterOptions: [{
                value: '1',
                label: '河北',
                jp: 'HB',
                qp: 'HeiBei',
            }, {
                value: '2',
                label: '河南',
                jp: 'HN',
                qp: 'HeiNan',
            }, {
                value: '3',
                label: '海南',
                jp: 'HN',
                qp: 'HaiNan',
            }],
            value: '3',
            groupOpt: [{
                label: '重庆',
                options: [{
                    value: 'lp',
                    label: '梁平'
                }, {
                    value: 'wz',
                    label: '万州',
                    disabled: true
                }]
            }, {
                label: '四川',
                options: [{
                    value: 'cd',
                    label: '成都'
                }, {
                    value: 'dz',
                    label: '达州'
                }]
            }],
            list: [],
            states: ["Alabama", "Alaska", "Arizona",
                "Arkansas", "California", "Colorado",
                "Connecticut", "Delaware", "Florida",
                "Georgia", "Hawaii", "Idaho", "Illinois",
                "Indiana", "Iowa", "Kansas", "Kentucky",
                "Louisiana", "Maine", "Maryland",
                "Massachusetts", "Michigan", "Minnesota",
                "Mississippi", "Missouri", "Montana",
                "Nebraska", "Nevada", "New Hampshire",
                "New Jersey", "New Mexico", "New York",
                "North Carolina", "North Dakota", "Ohio",
                "Oklahoma", "Oregon", "Pennsylvania",
                "Rhode Island", "South Carolina",
                "South Dakota", "Tennessee", "Texas",
                "Utah", "Vermont", "Virginia",
                "Washington", "West Virginia", "Wisconsin",
                "Wyoming"],
            loading: false,
            loading2: false,
            remoteOption: []
        }),
        watch: {
            value(val){
                console.log(val)
            }
        },
        mounted(){
            this.list = this.states.map(item => {
                return {value: item, label: item};
            });
        },
        methods: {
            change(val) {
                console.log(val)
            },
            remoteMethod(query) {
                if (query !== '') {
                    this.loading = true;
                    setTimeout(() => {
                        this.loading = false;
                        this.remoteOption = this.list.filter(item => {
                            return item.label.toLowerCase().indexOf(query.toLowerCase()) > -1;
                        });
                    }, 200);
                } else {
                    this.remoteOption = [];
                }
            },
            remoteMethod2(query) {
                if (query !== '') {
                    this.loading2 = true;
                    setTimeout(() => {
                        this.loading2 = false;
                        this.remoteOption = this.list.filter(item => {
                            return item.label.toLowerCase().indexOf(query.toLowerCase()) > -1;
                        });
                    }, 200);
                } else {
                    this.remoteOption = [];
                }
            },
            filter(val, item) {
                const input = val.toLocaleUpperCase();
                return item.label.startsWith(input) ||
                        item.jp.startsWith(input) ||
                        item.qp.startsWith(input);
            },
        }
    }
</script>


